body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.note {
    padding: 10px, 10px;
    font-size: 20px;
    margin: 0 auto;
    display: block;
    text-align: center;
    background: rgba(211, 211, 211, 0.6);
    border-radius: 15px;
    text-transform: capitalize;
  }
.forecast-each {
    display: inline;
  }
  .forecast-text-day {
    padding: 0px;
    font-size: 17px;
    text-align: center;
  }
  .forecast-text-temperature {
    padding: 0px;
    font-size: 17px;
    text-align: center;
  }
  .forecast-icon {
    padding: 0px;
    margin: 0 auto;
    display: block;
  }
.current-weather {
    font-size: 35px;
  }
  .current-icon {
    width: 50px;
  }
 
h1 {
    font-weight: 400px;
    text-transform: capitalize;
  }
  
  .current-city {
    font-size: 40px;
  }
  .today {
    font-size: 20px;
    display: inline;
  }
  .time {
    font-size: 15px;
    display: block;
  }
.card {
    display: block;
    margin: 0 auto;
    padding: 10px;
    margin-top: 20px;
    max-width: 500px;
  }
.open {
    display: block;
    text-align: center;
  }
  
document {
    font-family: "Roboto Slab", serif;
  }
